import React from 'react';
import { Statistic, Card, Row, Col, Spin, Typography } from 'antd';
import useDailyStats from './useDailyStats';

const { Title, Text } = Typography;

export default function DailySummary() {
  const { loading, error, stats } = useDailyStats(
    'https://covid19.mathdro.id/api/daily/'
  );

  if (loading) {
    return <Spin size="large" style={{ textAlign: 'center' }} />;
  }

  return (
    <div style={{ marginTop: '2em' }}>
      <Text strong style={{ fontSize: '1.3em' }}>
        Daily Summary
      </Text>

      <Row gutter={32} style={{ paddingBottom: '2em', textAlign: 'center' }}>
        <Col span={8}>
          <Card>
            <Statistic
              title="Confirmed"
              value={stats?.deltaConfirmed}
              valueStyle={{ color: '#c38755' }}
            />
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title="Recovered"
              value={stats?.deltaRecovered}
              valueStyle={{ color: '#3f8600' }}
            />
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title="Deaths"
              value={stats?.mainlandChina}
              valueStyle={{ color: '#cf1322' }}
            />
          </Card>
        </Col>
      </Row>
    </div>
  );
}
